"use client"

import { useEffect, useState } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar"
import { DashboardLayout } from "@/components/dashboard/dashboard-layout"
import { DashboardShell } from "@/components/dashboard-shell"
import { useProfile } from "@/components/hooks/useProfile"
import ProtectedRoute from "@/components/ProtectedRoute"

export default function ProfilePage() {
  const [isEditing, setIsEditing] = useState(false)
  const { 
    profilePic, 
    setProfilePic, 
    userName, 
    setUserName, 
    userRole, 
    isLoggedIn,
    isLoading,
    user
  } = useProfile()

  const [preview, setPreview] = useState<string | null>(null)
  const [userData, setUserData] = useState({
    name: "",
    dob: "1998-06-15",
    gender: "Male",
    email: "",
    phone: "+1 234 567 8900",
    university: "",
    address: "123 University Street, NYC",
  })

  useEffect(() => {
    // Load data from profile context when available
    if (profilePic) setPreview(profilePic)
    
    // Initialize user data from Apollo cache when available
    if (user) {
      setUserData({
        name: userName || "Your Name",
        dob: user.dob || "1998-06-15",
        gender: user.gender || "Male",
        email: user.email || "user@example.com",
        phone: user.phone || "+1 234 567 8900",
        university: user.university || "",
        address: user.address || "123 University Street, NYC"
      })
    } else {
      // Fallback to userName from context if no user in cache
      setUserData(prev => ({ 
        ...prev, 
        name: userName || "Your Name" 
      }))
    }
  }, [profilePic, userName, user])

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const url = URL.createObjectURL(file)
      setPreview(url)
      setProfilePic(url)
    }
  }

  const handleChange = (field: string, value: string) => {
    setUserData(prev => ({ ...prev, [field]: value }))
    if (field === "name") setUserName(value)
  }

  // Show loading state while authentication is being checked
  if (isLoading) {
    return (
      <DashboardLayout>
        <DashboardShell>
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-3xl font-bold">Profile</h2>
          </div>
          <div className="bg-white dark:bg-gray-900 border rounded-lg shadow p-6 space-y-6">
            <p className="text-center py-8">Loading profile data...</p>
          </div>
        </DashboardShell>
      </DashboardLayout>
    )
  }

  // Get role-specific UI configurations
  const getRoleSpecificUI = () => {
    switch(userRole.toLowerCase()) {
      case 'student':
        return {
          title: "Student Profile",
          sections: [
            { id: "education", label: "Education" },
            { id: "skills", label: "Skills & Interests" }
          ]
        }
      case 'university':
        return {
          title: "University Profile",
          sections: [
            { id: "programs", label: "Academic Programs" },
            { id: "stats", label: "University Statistics" }
          ]
        }
      case 'company':
        return {
          title: "Company Profile",
          sections: [
            { id: "industry", label: "Industry & Focus" },
            { id: "opportunities", label: "Opportunities" }
          ]
        }
      default:
        return {
          title: "User Profile",
          sections: []
        }
    }
  }

  const roleUI = getRoleSpecificUI()

  return (
    <ProtectedRoute allowedRoles={['student', 'university', 'company', 'admin']}>
      <DashboardLayout>
        <DashboardShell>
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-3xl font-bold">{roleUI.title}</h2>
            <Button onClick={() => setIsEditing(prev => !prev)}>
              {isEditing ? "Save" : "Edit Profile"}
            </Button>
          </div>

          {/* Profile Section */}
          <div className="bg-white dark:bg-gray-900 border rounded-lg shadow p-6 space-y-6">
            {/* Basic Info */}
            <div>
              <h3 className="text-xl font-semibold mb-4">Basic Info</h3>
              <div className="flex items-center gap-6">
                <Avatar className="h-16 w-16">
                  <AvatarImage
                    src={preview || ""}
                    alt={userData.name}
                    onError={(e) => {
                      e.currentTarget.onerror = null
                      e.currentTarget.src = ""
                    }}
                  />
                  <AvatarFallback className="bg-blue-100 text-gray-700 font-medium text-xl">
                    {userData.name
                      .split(" ")
                      .map(word => word[0])
                      .join("")
                      .toUpperCase()}
                  </AvatarFallback>
                </Avatar>
                <div className="flex-1">
                  <Label className="text-sm block mb-1">Upload Profile Picture</Label>
                  <Input 
                    type="file" 
                    accept="image/*" 
                    onChange={handleImageUpload} 
                    disabled={!isEditing} 
                  />
                </div>
              </div>
              <div className="grid md:grid-cols-2 gap-4 mt-4">
                <div>
                  <Label>Name</Label>
                  <Input
                    value={userData.name}
                    readOnly={!isEditing}
                    onChange={(e) => handleChange("name", e.target.value)}
                  />
                </div>
                <div>
                  <Label>Role</Label>
                  <Input
                    value={userRole ? userRole.charAt(0).toUpperCase() + userRole.slice(1) : "User"}
                    readOnly={true}
                    className="bg-gray-50"
                  />
                </div>
                <div>
                  <Label>Birthday</Label>
                  <Input
                    type="date"
                    value={userData.dob}
                    readOnly={!isEditing}
                    onChange={(e) => handleChange("dob", e.target.value)}
                  />
                </div>
                <div>
                  <Label>Gender</Label>
                  <Input
                    value={userData.gender}
                    readOnly={!isEditing}
                    onChange={(e) => handleChange("gender", e.target.value)}
                  />
                </div>
              </div>
            </div>

            {/* Contact Info */}
            <div>
              <h3 className="text-xl font-semibold mb-4">Contact Info</h3>
              <div className="grid md:grid-cols-2 gap-4">
                <div>
                  <Label>Email</Label>
                  <Input
                    value={userData.email}
                    readOnly={!isEditing}
                    onChange={(e) => handleChange("email", e.target.value)}
                  />
                </div>
                <div>
                  <Label>Phone</Label>
                  <Input
                    value={userData.phone}
                    readOnly={!isEditing}
                    onChange={(e) => handleChange("phone", e.target.value)}
                  />
                </div>
              </div>
            </div>

            {/* Address Info */}
            <div>
              <h3 className="text-xl font-semibold mb-4">Address</h3>
              <div>
                <Label>
                  {userRole === 'university' ? 'University Address' : 
                   userRole === 'company' ? 'Company Address' : 
                   'Current Address'}
                </Label>
                <Input
                  value={userData.address}
                  readOnly={!isEditing}
                  onChange={(e) => handleChange("address", e.target.value)}
                />
              </div>
            </div>

            {/* Role-specific sections */}
            {roleUI.sections.map(section => (
              <div key={section.id}>
                <h3 className="text-xl font-semibold mb-4">{section.label}</h3>
                <div className="p-4 border rounded-md bg-gray-50 dark:bg-gray-800">
                  <p className="text-gray-500 dark:text-gray-400 italic">
                    {isEditing 
                      ? `Add your ${section.label.toLowerCase()} information here` 
                      : `No ${section.label.toLowerCase()} information added yet`}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </DashboardShell>
      </DashboardLayout>
    </ProtectedRoute>
  )
}